@font-face {
    font-family: OpenSans-Semibold;
    src: url("./assets/fonts/OpenSans-Semibold.ttf");
}

.cc-mnt {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    width: 100%;
    min-height: 100vh;
    position: relative;
    background: -webkit-gradient(linear, left top, left bottom, from(#16161E), to(#192A3E));
    background: linear-gradient(to bottom, #16161E, #192A3E);
    background-image: url("./assets/PixSol_Bulls_Background_Image.png");;
}

.cc-mnt .mnt-bx {
    width: 370px;
    min-height: 450px;
    background: rgba(41, 41, 41, 0.95);
    border-radius: 15px;
    -webkit-box-shadow: 4px 5px 15px rgba(17, 17, 17, 0.747);
    box-shadow: 4px 5px 15px rgba(17, 17, 17, 0.747);
    position: relative;
    padding: 20px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
}

@media (max-width: 575.98px) {
    .cc-mnt .mnt-bx {
        width: 250px;
        min-height: 390px;
    }
}

.cc-mnt .mnt-bx .mnt-bx-head {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    margin-bottom: 20px;
}

.cc-mnt .mnt-bx .mnt-bx-img {
    text-decoration: none;
}

.cc-mnt .mnt-bx .mnt-bx-img img {
    width: 180px;
    height: 180px;
    -o-object-fit: cover;
    object-fit: cover;
    border-radius: 7px;
}

@media (max-width: 575.98px) {
    .cc-mnt .mnt-bx .mnt-bx-img img {
        width: 120px;
        height: 120px;
    }
}

.cc-mnt .mnt-bx .mnt-bx-name {
    color: #fecf16;
    text-align: center;
    font-family: OpenSans-Semibold;
    font-size: 18px;
    margin-bottom: 15px;
    margin-top: 0;
}

.cc-mnt .mnt-bx .mnt-bx-name span {
    margin-left: 2px;
    /* font-size: 20px; */
    color: #fff;
}

.cc-mnt .mnt-bx .mnt-bx-btn {
    background-color: #FED117;
    font-family: OpenSans-Semibold;
    color: black;
    margin-top: 10px;
    -webkit-transition: all 400ms cubic-bezier(0.29, 0.9, 0.26, 1.21);
    transition: all 400ms cubic-bezier(0.29, 0.9, 0.26, 1.21);
}

.cc-mnt .mnt-bx .mnt-bx-btn:hover {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
    border-radius: 2px;
}

.btn,
.frm-bx-btn,
.cc-mnt .mnt-bx .mnt-bx-btn {
    outline: none !important;
    cursor: pointer;
    min-width: 150px;
    height: 40px;
    border: 0;
    padding: 0px 4px;
    border-radius: 8px;
    font-size: 14px;
}
.cc-logo{
    position: fixed;
    left: 2%;
    bottom: 3%;
    width: 140px;
}
.home-link{
    position: absolute;
    left: 2%;
    top: 3%;
    /* width: 140px; */
    font-size: 18px;
    color: #FED117;
    font-family: OpenSans-Semibold;
}
.MuiButton-label{
    justify-content: center !important;
    font-size: 18px;
}